import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ToastController, Events } from 'ionic-angular';
import { UserServiceProvider } from '../../providers/user-service/user-service';
import { CitiesProvider } from '../../providers/cities/cities';

@IonicPage()
@Component({
  selector: 'page-user-form',
  templateUrl: 'user-form.html',
})
export class UserFormPage {

  index
  user = {
    u_id: null
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,

    private cities: CitiesProvider,
    private userService: UserServiceProvider,

    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController,

    private events: Events
  ) {
    this.index = this.navParams.get('index')
    this.user.u_id = this.navParams.get('u_id')

    let loading = this.loadingCtrl.create({ content: 'Ltfen Bekleyiniz...' })

    loading.present()

    this.userService.getUserDetailsById(this.user.u_id).subscribe((response) => {
      if (response['_body']) {
        console.log(response.json())
        this.user = response.json().user
  
        loading.dismiss()
      } else {
        loading.dismiss()

        let toast = this.toastCtrl.create({
          message: 'Kullanc kayd bulunamad. Kullanc silinmi veya tanm olabilir',
          closeButtonText: 'OK',
          showCloseButton: true
        })

        toast.present()

        this.navCtrl.pop()
      }
    })
  }

  onSaveButtonClicked() {
    let alert = this.alertCtrl.create({
      title: 'Kullanc Gncellensin Mi?',
      buttons: [
        {
          text: 'Geri Dn'
        },
        {
          text: 'Gncelle',
          handler: () => {
            this.updateUser()
          }
        }
      ]
    })

    alert.present()
  }

  updateUser() {
    let loading = this.loadingCtrl.create({ content: 'Kullanc Gncelleniyor...' })

    loading.present()

    this.userService.updateUser(this.user.u_id, this.user).subscribe((response) => {
      loading.dismiss()

      let toast = this.toastCtrl.create({
        message: 'Kullanc gncellendi. Deiiklikler uygulanmadysa yenile butonunu kullannz',
        duration: 1500
      })

      toast.present()


      this.events.publish('userUpdated', {
        index: this.index,
        user: this.user
      })

      this.navCtrl.pop()
    })
  }

}



